@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 25px 10px;
}
footer p {
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #272a31;
}
footer img {
  filter: grayscale(0.9);
  width: 160px;
}

.hero {
  background: url("../img/educationbg.png");
  background-color: #d3d3d3;
}

header {
  position: fixed;
  background-color: white;
  width: 100%;
  top: 0;
  padding: 25px;
  z-index: 10;
}
@media (min-width: 1025px) {
  header {
    padding: 20px 15vw;
  }
  header img {
    filter: grayscale(0.5);
  }
}
header nav .sign-up {
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
  color: #ec5242;
  border: 5px solid #ec5242;
  width: 190px;
}
@media (min-width: 768px) and (max-width: 1400px) {
  header nav .sign-up {
    font-size: 16px;
    width: 150px;
  }
}
header nav .shadow-menu {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  background-color: #ec5242;
  display: flex;
  flex-direction: column;
  padding: 25px 27px;
  visibility: hidden;
}
header nav .shadow-menu close-wrap {
  opacity: 1;
}
header nav .shadow-menu close-wrap i {
  opacity: 1;
}
header nav .shadow-menu svg {
  width: 50px;
  height: 50px;
}
header nav .shadow-menu ul {
  opacity: 1;
  padding: 30px 40px;
  gap: 45px;
}
@media (min-width: 768px) {
  header nav .shadow-menu ul {
    gap: 5px;
  }
}
header nav .shadow-menu ul li a {
  color: white;
  font-size: 32px;
}
header nav .shadow-menu ul li .sign-up {
  color: white;
  border: 4px solid #d3d3d3;
}
header nav .showsmenu {
  visibility: visible;
}
header nav svg {
  height: 25px;
  width: 25px;
}
header nav ul, header nav a {
  gap: 5vw;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #999da5;
  cursor: pointer;
  margin: 0;
  text-decoration: none;
}
@media (min-width: 768px) and (max-width: 1400px) {
  header nav ul, header nav a {
    font-size: 18px;
    gap: 2.5vw;
  }
}
header nav ul a:hover, header nav a a:hover {
  color: #ec5242;
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

hr {
  margin-top: 24px;
  border: 1px solid #ec5242;
  width: 50%;
  margin-inline: auto;
}

body {
  width: 100%;
}

ul,
li {
  padding: 0;
  list-style: none;
}

@font-face {
  font-family: "COCOGOOSE";
  src: url("../../../dist/assets/fonts/Cocogoose_trial.eot") format("eot"), url("../../../dist/assets/fonts/Cocogoose_trial.woff") format("woff"), url("../../../dist/assets/fonts/Cocogoose_trial.ttf") format("truetype"), url("../../../dist/assets/fonts/Cocogoose_trial.svg#COCOGOOSE") format("svg");
  font-weight: normal;
  font-style: normal;
}
.headline {
  padding: 80px 25px;
  margin-top: 60px;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #ec5242;
}
.headline p {
  font-size: 16px;
}
@media (min-width: 768px) {
  .headline p {
    font-size: 2rem;
    line-height: 2rem;
  }
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 2rem;
  font-weight: 700;
  line-height: 34px;
  text-transform: uppercase;
  color: #ec5242;
}
@media (min-width: 768px) {
  .headline h1 {
    font-size: 3rem;
    line-height: 3rem;
  }
}
.headline .headline-box {
  background-color: #f7f7f7;
  border: 1px solid white;
  padding: 20px;
  margin: 20px 0;
}
.headline .headline-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .headline .headline-box p {
    font-size: 2rem;
    line-height: 2rem;
  }
}
.headline .date-event {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  color: #272a31;
  margin: 30px 0;
}
.headline .adress {
  color: #272a31;
}

.partners-logo {
  background-color: #272a31;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  padding: 50px 25px;
  text-align: center;
  color: white;
}
.partners-logo hr {
  width: 15%;
}
.partners-logo .wrap-logos ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  flex-wrap: wrap;
}
.partners-logo img {
  width: 90px;
  margin: 20px 10px;
  filter: grayscale(1);
}
@media (min-width: 768px) {
  .partners-logo img {
    margin: 20px 50px;
    width: 168px;
  }
}

.headline {
  text-align: center;
}
.headline p {
  font-size: 16px;
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 32px;
  font-weight: 700;
  line-height: 34px;
  text-transform: uppercase;
  color: #ec5242;
}
@media (min-width: 768px) {
  .headline h1 {
    font-size: 3em;
    line-height: 1em;
  }
}
.headline .about-box {
  background-color: white;
  border: 1px solid #d3d3d3;
  padding: 20px;
  text-align: center;
  margin: 15px 5px;
}
.headline .about-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .headline .about-box p {
    font-size: 18px;
  }
}
.headline .about-headline-contact {
  font-family: "Lato", sans-serif;
  font-size: 15px;
  font-weight: 500;
  color: #272a31;
  margin: 25px 0;
}
@media (min-width: 768px) {
  .headline .about-headline-contact {
    font-size: 18px;
  }
}
.headline .about-headline-contact strong {
  text-decoration: underline;
}

.course-logo {
  padding: 20px 25px;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
}
.course-logo p {
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}
.course-logo img {
  padding: 15px 34px;
  border: 1px solid #d3d3d3;
  filter: grayscale(0.9);
  width: 100%;
  max-width: 400px;
}

.last-events {
  padding: 20px 25px;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.last-events .last-events-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.last-events .last-events-wrap .events {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 400 px;
  color: white;
  height: 250px;
  width: 100%;
  background-color: #ec5242;
}
.last-events p {
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}

section {
  margin: 35px 0;
}

hr {
  width: 10%;
}